<template>
    <div :post="post">

        <header class="masthead" :style="{backgroundImage: 'url(' + post.cover_pic + ')'}">
            <div class=""></div>
            <div class="container">
                <div class="row">
                    <div class="col-lg-8 col-md-10 mx-auto">
                        <div class="post-heading">
                            <h1 style="color: white;">{{post.title}}</h1>
                            <h2 class="subheading" style="color: white;">{{post.snippet}}</h2>
                            <span class="meta">Posted by
                <a href="#">{{post.user}}</a>
                on {{post.createdAt | date}}</span>
                        </div>
                    </div>
                </div>
            </div>
        </header>
        <article>
            <div class="container">
                <div class="row">
                    <div class="col-lg-8 col-md-10 mx-auto">
                        <div v-html="compiledMarkdown"></div>
                    </div>
                </div>
            </div>
        </article>
<div v-if="post.published"></div>
<div v-else>
  <div class="row">
    <div class="col-md-2"></div>
    <div v-if="admin" class="col-md-4">
      <button class="btn btn-primary btn-block" @click="publish">Publish</button>
    </div>
    <div class="col-md-4">
      <router-link class="btn btn-primary btn-block" :to='{name: "edit", params: {slug: post.slug, post: post}}'>Edit</router-link>
    </div>
<div class="col-md-2"></div>
  </div>  
</div>
    </div>

</template>

<script>
    import marked from 'marked';
	import axios from 'axios';

    export default {
        name: "Post",
	props: { id: String },
        data () {
            return {
                post: {
             },
admin: !!localStorage.blog_auth_admin
            }
        },
methods:{
publish(){
  this.post.published = true;
  this.post.published_at = new Date();
  axios
  .put(`https://glacial-gorge-75625.herokuapp.com/posts/${this.post.id}`, this.post, {
            headers: {
               'Authorization': `Bearer ${localStorage.getItem('blog_auth_jwt')}`
            }
  })
}
},
mounted: function(){
  console.log(this.$route.params.id);
  axios
    .get(`https://glacial-gorge-75625.herokuapp.com/posts/?slug=${this.$route.params.slug}`)
    .then(response => {
    let post = response.data[0];
    this.compiledMarkdown = marked(post.content, { sanitize: true });
    post.createdAt = new Date(post.createdAt);
    this.post = post;
  })
    .catch(error => {
    console.error(error);
  })
}
    }
</script>

