<script>
    import axios from 'axios';
export default {
  data(){
    return {
      email: '',
      password: ''
    }
  },
  methods: {
name () {
console.log(this.email);},
    login () {
      axios
        .post('https://glacial-gorge-75625.herokuapp.com/auth/local/', {
        identifier: this.email,
        password: this.password
      })
        .then(response => {
        console.log(response.data.jwt);
        localStorage.setItem('blog_auth_jwt', response.data.jwt);
        localStorage.setItem('blog_auth_user', response.data.user.username);
        console.log(response.data.user.role);
        if(response.data.user.role.name.toLowerCase() === 'administrator'){
          localStorage.setItem('blog_auth_admin', 'true');
        } 
	//this.$store.logged_in = true;
        //this.$router.push('/')
	window.location = '/home'
      })
        .catch(error => {
        console.log('An error occurred', error);
      })
    }
  }
};
