<template>
    <section id="posts" class="section mt-5">

        <div :key="post.id" v-for="post in posts">
            <PostPreview
                    :key="post.id"
		    :slug="post.slug"
                    :title="post.title"
                    :snippet="post.snippet"
                    :cover_pic="post.cover_pic"
                    :tags="post.tags"
		    :user="post.user"
		    :published_at="post.createdAt"
                    :id="post.id">

            </PostPreview>
        </div>
    </section>
</template>

<script>
    import PostPreview from './PostPreview'
    import axios from 'axios';

    export default {
  components: {
    PostPreview
  },
  data () {
    return {
      posts: []
    }
  },
  methods: {
    update (){
      let published = false, params = '';
      if(!localStorage.blog_auth_admin){
        params  = `&user=${localStorage.blog_auth_user}`
      }
      if(this.$router.currentRoute.path === "/home")
        published=true

      axios
        .get(`https://glacial-gorge-75625.herokuapp.com/posts?published=${published}${params}`)
        .then(response => {
        this.posts = response.data.map(data => ({
          ...data,
          createdAt: new Date(data.createdAt)
        }))})
        .catch(error => {
        console.error(error);
      })
    }
  },
  created: function (){
    this.update()
  }
}
</script>

<style scoped>
    #posts {
        padding-top: 2rem;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;
    }

    @media (min-width: 35rem) {
        #posts {
            flex-direction: row;
            flex-wrap: wrap;
        }
    }
</style>
