<template>
    <section id="posts" class="section mt-5">
        <div class="container">
            <form @submit.prevent="submit">
                <div class="form-group">
                    <input type="text" class="form-control" v-model="post.title" @change="submit" placeholder="Untitled Post">
                </div>
                <div class="form-group">
                    <textarea class="form-control" v-model="post.snippet"  rows="3" placeholder="Enter short snippet to be displayed on first page"></textarea>
                </div>
                <div class="form-group">
                    <vue-dropzone ref="myVueDropzone" id="dropzone" @vdropzone-file-added="upload" :options="dropzoneOptions">Drop Files Here or Click to Upload</vue-dropzone>
                </div>
                <div class="form-group">
                    <markdown-editor v-model="post.content"></markdown-editor>
                </div>
                <div class="form-group">
                    <label class="">Tags</label>
                    <multi-select @search-change="getTags" v-model="post.tags" tag-placeholder="Add this as new tag" placeholder="Search or add a tag" label="name" track-by="id" :options="options" :multiple="true" :taggable="true" :searchable="true" @tag="addTag"></multi-select>
		<span v-for="tag in post.tags" class="badge badge-default" :key="tag.id">{{ tag.name }}</span>
                </div>
                <div class="text-center">
                    <base-button type="primary" native-type="submit" class="my-4">Submit</base-button>
                </div>
            </form>
        </div>

    </section>
</template>

<script>
    import vue2Dropzone from 'vue2-dropzone'
    import 'vue2-dropzone/dist/vue2Dropzone.min.css'
    import axios from 'axios';

    import 'vue-multiselect/dist/vue-multiselect.min.css'
    import Multiselect from 'vue-multiselect'

    import PostService from './PostService';
	const url =  'https://glacial-gorge-75625.herokuapp.com/posts/';
	const token = localStorage.getItem('blog_auth_jwt');
	const config = {headers: {'Authorization': `Bearer ${token}`}};

    export default {
   name: "Entry",
   components: {
      vueDropzone: vue2Dropzone,
      'multi-select': Multiselect,
   },
props: {postID: String},
   data () {
      return {
         post: {
            title: '',
            tags: [],
            content: '',
	    cover_pic: '',
            snippet: '',
            
         },
         options: [],
         dropzoneOptions: {
            url: '',
            thumbnailWidth: 150,
            maxFilesize: 0.5,
            headers: { "My-Awesome-Header": "header value" },
            dictDefaultMessage: "<i class='fa fa-cloud-upload'></i><h4>DROP COVER IMAGE TO UPLOAD</h4>"
         }
      }
   },
   methods: {
      async addTag(tag) {
         let newTag = await PostService.InsertTag(tag);
         console.log(newTag);
         /*this.options.push(tag);
                  this.post.tags.push(tag);*/
      },
      async upload(file){
         let formData = new FormData();
         formData.append("files", file);
         formData.append("refId", this.post.id); // User's Id.
         formData.append("ref", "post"); // Model name.
         formData.append("field", "cover-pic"); // Field name in the User model

         axios.post('https://glacial-gorge-75625.herokuapp.com/upload', formData, {
            headers: {
               'Content-Type': 'multipart/form-data',
               'Authorization': `Bearer ${token}`
            }
         })
            .then(response => {
console.log(response.data[0].url);
            this.post.cover_pic = response.data[0].url
         })
	   .then(response => {
		this.submit();
	})	
            .catch(err => {
            console.log(err);
         })
      },
      async submit() {
         if (!this.post.id) {
	    this.post.user = localStorage.getItem('blog_auth_user')
            this.post.slug = this.slugify(this.post.title);
            //let post = await PostService.InsertPost(this.post);
            axios
               .post(url, this.post, config)
               .then(response => {
               this.post = response.data;
            })
               .catch(error => {
               console.error(error);
            })
         }
         else {
            this.post.slug = this.slugify(this.post.title);
            
            axios
               .put(`${url}${this.post.id}`, this.post, config)
               .then(response => {
               this.post = response.data;
            })
               .catch(error => {
               console.error(error);
            })
            console.log(this.post);
         }
      },
      slugify (title) {
         let slug = title.toString().toLowerCase()
         .replace(/\s+/g, '-') // Replace spaces with -
         .replace(/&/g, '-and-') // Replace & with 'and'
         .replace(/[^\w\-]+/g, '') // Remove all non-word chars
         .replace(/--+/g, '-'); // Replace multiple - with single -
         const today = new Date();
         slug = `${slug}-${today.getFullYear()}-${today.getMonth()}-${today.getDate()}`
         return slug;
      },
      async getTags () {
         let options = await PostService.GetTags();
         console.log(options);
      }
   },
   mounted: function(){
      axios
         .get('https://glacial-gorge-75625.herokuapp.com/tags')
         .then(response => {
         this.options = response.data;
      })
         .catch(error => {
         console.error(error);
      })
axios
    .get(`https://glacial-gorge-75625.herokuapp.com/posts/?slug=${this.$route.params.slug}`)
    .then(response => {
  this.post = response.data[0];
})

.catch(err => console.log(err))

   }
}
</script>

