import axios from 'axios';
//import config from './config';

const url =  'https://glacial-gorge-75625.herokuapp.com/posts/';
const token = localStorage.getItem('blog_auth_jwt');
const config = {
	headers: {'Authorization': `Bearer ${token}`}
};

export default class PostService{

    static FindAll(params) {
        axios
            .get(url,{
                params: params
            })
            .then(response => {
                return response.data.toArray();
            })
            .catch(error => {
                console.error(error);
            })
    }

    static GetTags() {
        axios
            .get('https://glacial-gorge-75625.herokuapp.com/tags')
            .then(response => {
                return response.data;
            })
            .catch(error => {
                console.error(error);
            })
    }

    static FindOne(id){
        axios
            .get(`${url}/${id}`)
            .then(response => {
                return response.data;
            })
            .catch(error => {
                console.error(error);
            })
    }

    static InsertPost(newPost){
  return new Promise(async (resolve, reject) => {
    try {
      const res = axios.post(url, newPost, config);
      const data = res.data;
      resolve(data);
    } catch(err){
      reject(err);
    }
  })
}

    static async InsertTag(newTag){
        axios
            .post('https://glacial-gorge-75625.herokuapp.com/tags', {"name": newTag})
            .then(response => {
                return response.data;
            })
            .catch(error => {
                console.error(error);
            })
    }

    static DeletePost(postID){
        axios
            .delete(`${url}/${postID}`)
            .then(response => {
                return response.data;
            })
            .catch(error => {
                console.error(error);
            })
    }

    static UpdatePost(post){
        axios
            .put(`${url}/${post.id}`, 
                post,{
                headers: {
                    Authorization: `Bearer ${localStorage.blog_auth.jwt}`
                }
            })
            .then(response => {
                return response.data;
            })
            .catch(error => {
                console.error(error);
            })
    }

    static GetBySlug(slug){
        axios
            .get(`${url}/slug?=${slug}`)
            .then(response => {
                return response.data;
            })
            .catch(error => {
                console.error(error);
            })
    }
}
