import Vue from "vue";
import Router from "vue-router";
import AppFooter from "./layout/AppFooter";
import Components from "./views/Components.vue";
import Login from "./views/Login.vue";
import Register from "./views/Register.vue";
import FrontPage from "./blog/FrontPage";
import Entry from "./blog/Entry";
import Post from "./blog/Post";
import Drafts from "./blog/Drafts";
import About from "./blog/About";
import StarterHeader from './layout/starter/StarterHeader';

Vue.use(Router);

export default new Router({
    linkExactActiveClass: "active",
    routes: [
        {
            path: "/",
            redirect: '/home'
            
        },
        {
            path: "/login",
            name: "login",
            components: {
                header: StarterHeader,
                default: Login,
                footer: AppFooter
            }
        },
        {
            path: "/register",
            name: "register",
            components: {
                header: StarterHeader,
                default: Register,
                footer: AppFooter
            }
        },
        {
            path: "/about",
            name: "about",
            components: {
                header: StarterHeader,
                default: About,
                footer: AppFooter
            }
        },
        {
            path: "/home",
            name: "home",
            components: {
                header: StarterHeader,
                default: FrontPage,
		footer: AppFooter
            }
        },
{
            path: "/drafts",
            name: "drafts",
            components: {
                header: StarterHeader,
                default: Drafts,
		footer: AppFooter
            }
        },
        {
            path: "/blog/:slug",
            name: "blog",
            components: {
                header: StarterHeader,
                default: Post,
		footer: AppFooter,
		props: { default: true }
            }
        },
        {
            path: "/new",
            name: "new",
            components: {
                header: StarterHeader,
                default: Entry
            }
        },
        {
            path: "/edit/:slug",
            name: "edit",
            components: {
                header: StarterHeader,
                default: Entry,
		props: { default: true }
            }
        }
    ],
    scrollBehavior: to => {
        if (to.hash) {
            return { selector: to.hash };
        } else {
            return { x: 0, y: 0 };
        }
    }
});
