<script>
    import axios from 'axios';

    export default {
        data(){
            return {
                name: '',
                email: '',
                password: ''
            }
        },
        methods: {
            register () {
                axios
                    .post('https://glacial-gorge-75625.herokuapp.com/auth/local/register', {
                        username: this.name,
                        email: this.email,
                        password: this.password
                    })
                    .then(response => {
                        localStorage.blog_auth_jwt = response.data.jwt;
                        this.$router.push('/')
                    })
                    .catch(error => {
                        console.log('An error occurred', error);
                    })
            }

        }
    };
</script>
